.form-field{margin-bottom:2em;text-align:left}.form-field>label{display:inline-block;margin-bottom:.75em;margin-left:1.5em;font-size:13px;font-weight:600;color:#3b4859}.form-field--input{color:#a6c5cc;position:relative}.form-field--input>svg{position:absolute;bottom:1em;left:1em}.form-field--input>input{width:100%;height:50px;border-radius:20px;border:1px solid #d2e3e7;font-family:Open Sans,sans-serif;font-size:14px;font-weight:600;color:inherit;outline:none;padding:1em 1.5em 1em 3em;transition:.3s}.form-field--input>input:focus,.form-field--input>input:hover{border-color:#a6c5cc}.form-field--input>input:focus{box-shadow:0 0 3px 3px rgba(166,197,204,.5)}